@import 'variables';

$nav-small-height: 7rem;
$hr-height: 0.1rem;

html {
  font-size: 1vmin;
}

body {
  height: 100vh;
  /* this shouldn't be necessary, but it is!? */
  box-shadow: inset $orange 0 0 0 0.2rem;
  overflow: hidden;
}

hr {
  border: none;
  background-color: $light-gray;
  height: $hr-height;
}

.main {
  left: 50vw;
  top: 50vh;
  transform: translate(-50%, -50%);
  position: relative;

  /*padding-left: 2.5vw;*/
  /*padding-right: 2.5vw;*/
  /*padding-top: 1vh;*/
  /*padding-bottom: 1vh;*/
  width: 95vw;
  height: 98vh;
  /*width: 100%;*/
  /*height: 100%;*/
}

.nav-line {
  transition-duration: 0.1s;
  transition-timing-function: linear;

  stroke: $light-gray;
  stroke-width: 1.5;
  z-index: 0;
}

.nav-line-hover {
  stroke: $orange;
  stroke-width: 3;
}

.nav-line-clickable {
  stroke-width: 20;
  stroke: rgb(255, 255, 255);
  opacity: 0;
  z-index: 1000;
  cursor: pointer;
}

.nav-text {
  transition: opacity 1s, fill 0.1s;

  font-family: 'Playfair Display', serif;
  @include font(27, 30, 50);
  //font-size: 28px;
  //font-weight: 400;
  //letter-spacing: 2px;
  text-transform: uppercase;
  fill: $dark-gray;
  opacity: 100;
  cursor: pointer;

  -moz-user-select: -moz-none;
  -ms-user-select: none;
  user-select: none;

  &:hover {
    fill: $orange;
  }
}

.nav-small .nav-text {
  opacity: 0;
}

.nav-container {
  transition-duration: 1s;
  width: 100%;
  height: 100%;
  position: relative;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  &.nav-small {
    height: $nav-small-height;
  }

  hr {
    display: none;
    width: 45%;

    &.active {
      display: block;
    }
  }
}

.nav {
  transition-duration: 0.4s;
  transition-delay: 0s;
  transition-timing-function: ease-out;
  max-width: 70%;
  max-height: 70%;
  width: 100%;
  height: 100%;

  overflow: visible;

  &.nav-small {
    transition-duration: 0.4s;
    transition-delay: 0.6s;
    transition-timing-function: ease-in;

    max-width: 100%;
    max-height: 100%;
    cursor: pointer;
  }

  &.nav-small-finished {
    width: 12rem;
    transition: color 0s; /* do not transition the width change */
  }
}

.nav-small .nav-line {
  transition-duration: 1s;
  stroke-width: 7;
}

.contentpane {
  top: 3.4rem; /* half of .nav-small height - 1px */
  width: 100%;
  height: calc(100% - (3.4rem + 3.5rem));
  position: absolute;

  z-index: -1;
  display: none;

  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.contentpane.active {
  display: flex;
}

.contentpane-left {
  width: 40%;
  height: 100%;
  float: left;
  //margin-left: 5%;
}

.contentpane-center {
  width: 20%;
  height: 100%;
}

.contentpane-right {
  width: 40%;
  height: 100%;
  float: right;
  //margin-right: 5%;
}

#left-top, #center-top, #right-top {
  height: 20%;
}

#left-mid, #center-mid, #right-mid {
  height: 60%;
}

#left-bottom, #center-bottom, #right-bottom {
  height: 20%;
}

.topic {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;

  text-transform: uppercase;
  color: $dark-gray;
  text-align: right;
}

.topic h1 {
  margin: 0;
  //font-size: 4.5rem;
  //line-height: 4.3rem;
  //letter-spacing: 0.08rem;
  @include font(61, 20, 62);
}

.topic-single {
  display: none;
  vertical-align: middle;

  &.active {
    display: inline-block;
  }

  h2 {
    font-family: 'Merriweather Sans', sans-serif;
    @include font(22, 120, 22);
    color: $orange;
  }
}

.topic-synthesis {
  display: none;
  vertical-align: middle;

  &.active {
    display: inline-block;
  }

  hr {
    width: 25rem;
    margin-right: 0;
    background-color: $orange;
  }
}

.content-container {
  width: calc(100% - (2 * 4.5rem));
  padding-left: 4.5rem;
  height: calc(100% - (3.4rem + 1.5rem));
  padding-top: 3.4rem;
  padding-bottom: 1.5rem;
  border: none;
  padding-right: 0.5rem;
}

.content-container2 {
  max-height: 100%;
  overflow-y: auto;
  overflow-x: hidden;

  top: 50%;
  transform: translateY(-50%);
  position: relative;
}

.button {
  width: $button-height;
  height: $button-height;
  cursor: pointer;
}

