//  position: relative;
//  display: flex;
//  flex-direction: column;
//  justify-content: space-between;
//  align-items: center;
//}

#center-mid {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

#buttons-center-1 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

#buttons-center-3 {
  height: 3 * $button-height;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

#text-button {
  margin-bottom: $button-height;
}

//#contentpane-center .button {
//  margin-top: $button-height;
//}

#title {
  color: $light-gray;
  text-transform: uppercase;
  text-align: center;
  //font-weight: 400;
  //letter-spacing: 2px;

  padding-top: ($nav-small-height/2) + 2rem;

  .title-small {
    //font-size: 20px;
    font-family: "Merriweather Sans", sans-serif;
    @include font(16, 15, 25)
  }

  .title-large {
    //font-size: 26px;
    font-family: 'Playfair Display', serif;
    @include font(26, 15, 25)
  }
}
